import React, { Component } from 'react';
import styled from 'styled-components';

const Container = styled.div`
  position: fixed;
  left: 0px;
  top: 80px;
`;

const Item = styled.div`
  font-size: 14px;
  padding-left: 12px;
  color: #1a2d5ac2;

  &:hover {
    cursor: pointer;
    color: #1A2D5A;
  }

  &.h1 {
    font-size: 16px;
  }

  &.h2 {
    font-size: 16px;
    padding-left: 24px;
  }

  &.h3 {
    padding-left: 36px;
  }
`;

const tags = ['h1', 'h2', 'h3'];

export class ArticleLayout extends Component {

  constructor(props) {
    super(props);

    this.state = {
      headings: [],
      visible: true,
    };
  }

  componentDidMount() {
    const blogContainer = document.querySelector('.blog-post-content');
    if (!blogContainer) return;

    const headings = [];
    for (let i = 0; i < blogContainer.children.length; i++) {
      const elem = blogContainer.children.item(i);
      const tag = elem.tagName.toLowerCase()
      if (tags.includes(tag)) {
        headings.push({
          type: tag,
          value: elem.textContent,
          elem,
        });
      }
    }

    this.setState({
      headings,
    });

    window.addEventListener('resize', this.windowSizeChanged, this.windowSizeChanged);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.windowSizeChanged, this.windowSizeChanged);
  }

  windowSizeChanged = () => {
    this.setState({
      visible: window.innerWidth >= 1024,
    });
  }

  handleIndexOnClick = index => {
    const obj = this.state.headings[index];
    if (!obj) return;
    if (!obj.elem) return;

    obj.elem.scrollIntoView({
      behavior: "smooth",
    });
  }

  renderContent() {
    return this.state.headings.map(({ type, value }, index) => {
      return (
        <Item
          key={value}
          className={type}
          onClick={() => this.handleIndexOnClick(index)}
        >
          { value }
        </Item>
      )
    });
  }

  render() {
    return (
      <Container style={{
        display: this.state.visible ? 'block' : 'none',
      }}>
        {this.renderContent()}
      </Container>
    );
  }
}
